header {
  background-color:  #570041;
  height: 36rem;
}

.header {
  padding-top: 1rem;
  padding-left: 6rem;
}

.header__h1 {
  color: white;
  min-width: 4rem;
  font-size: 4rem;
}

.header__p {
  color: white;
  font-size: 16px;
  max-width: 260px;
  padding-top: 2rem;
  line-height: 1.5;
}

.header__btn {
  
  margin-top: 1.3rem;
}


.header__img {
  height: 29rem;
  position: absolute;
  left: 28rem;
  top: 8rem;
}


@media(max-width:67.5rem) {
  header {
    height: 30rem;
  }
.header__img {
  height: 25rem; 
  bottom: 7rem;
  left: 22rem;
}

.header__h1 {
  font-size: 4rem;
}

.header {
  padding-top: 1.3rem;
  padding-left: 2rem;
}
}

@media(max-width:67.5rem) {
  .header__img {
   height: 20rem; 
  bottom: 14rem;
  left: 23rem;
}

.header {
  padding-top: 5rem;
  padding-left: 5rem;
}
.header__h1 {
  font-size: 3.2rem;
}

.header__p {
  font-size: 14px;
}

.header__btn {
  padding: 0.7rem;
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 500;
}

}

@media(max-width: 48rem) {
     .header__img {
   height: 18rem; 
  bottom: 13rem;
  left: 19rem;
}
.header__h1 {
  font-size: 3rem;
}

.header {
  padding-top: 7.5rem;
  padding-left: 2rem;
}

.header__p {
  font-size: 14px;
   min-width: 160px;
}

}

@media(max-width: 48rem) {
     .header__img {
   height: 16rem; 
  bottom: 14rem;
  left: 15rem;
}
.header__h1 {
  font-size: 2.4rem;
}

.header {
  padding-top: 8.5rem;
  padding-left: 2rem;
}

.header__p {
  font-size: 14px;
   min-width: 120px;
}
   
}


@media(max-width: 48rem) {
  header {
  height: 40rem;
}
   .header__img {
   display: none;
}

.header {
  padding-top: 8.5rem;
  padding-left: 5rem;
}

.header__h1 {
  font-size: 3.5rem;
}

.header__p {
  font-size: 18px;
  min-width: 140px;
}

.header__btn {
  padding: 0.8rem;
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 600;
}
}

@media(max-width: 36rem) {
  header{
    height: 32rem;
    text-align: center;
  }
.header__h1 {
  font-size: 2.5rem;
  text-align: center;
  min-width: 180px;
}

.header__p {
  font-size: 15px;
  min-width: 160px;
  text-align: center;
  font-weight: 500;
}
.header {
  padding-top: 5rem;
  padding-right: 5rem;
}

.header__btn {
  padding: 0.7rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 600;
  padding-left: 1.9rem;
}
}